<template>
  <div v-if="$store.getters.isInitialSetup">

    <div class="modal setup-modal" :class="{active: $store.getters.isInitialSetup}" >
      <div class="modal-container setup-progress" >
        <div class="setup-image" >
          <img src="static/img//Onion-Logo-Full.png" alt="">
        </div>
        <div class="bar bar-slider">
          <div v-for="(step, idx) in steps" :key="idx"  class="bar-item" :style="{width: `${idx/(steps.length-1)*100}%`,
          background: getProgressColor(idx), 'z-index': steps.length-idx}">
            <button class="bar-slider-btn btn step-btn" :style="{background: getProgressColor(idx)}">
              <div class="progress-title" :style="{color: getProgressColor(idx)}">{{step.title}}</div>
            </button>
          </div>
        </div>
      </div>

      <div class="modal-container setup-steps" v-for="(step, idx) in steps" v-if="currentStep === idx" :key="idx">

        <!-- Header -->
        <div class="modal-header">
          <!-- <img  src="static/img/OnionOS.svg"> -->
          <h3> {{step.heading}} </h3>
        </div>

        <!-- Body -->
        <div class="modal-body">
          <div class="setup-instruction" v-html="step.body"></div>

          <div class="setup-component" v-if="step.component">
            <component
              :is="step.component"
              @success="currentStep ++"
              @next="step.next = true"
            >
            </component>
          </div>
        </div>

        <!-- Footer -->
        <div class="modal-footer">
          <button class="btn" v-if="step.next" @click="next">Continue</button>
          <button class="btn" v-if="step.done" @click="done">Enter OnionOS</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoginForm from '@/components/LoginForm'
import WifiClientConfig from '@/components/WifiClientConfig'
import SoftwareUpgrade from '@/components/SoftwareUpgrade'

export default {
  name: 'SetupView',
  components: {
    LoginForm,
    WifiClientConfig,
    SoftwareUpgrade
  },
  data () {
    return {
      initialSetup: true,
      currentStep: 0,
      steps: [
        {
          title: 'Welcome',
          heading: 'Welcome to the Omega2 Setup Wizard',
          body: 'Let\'s get your Omega2 ready to make some amazing things!<br><br>We\'ll connect to a WiFi network and then upgrade the device software. If you get stuck, check out our <a href="http://onioniot.com/getstarted" target="_blank">getting started guide</a>.',
          next: true
        },
        {
          title: 'Login',
          // heading: 'Login',
          body: 'First, login to your Omega',
          component: 'LoginForm',
          next: false
        },
        {
          title: 'WiFi',
          heading: 'Connect to WiFi',
          // body: 'First we need to log in to you Omega',
          component: 'WifiClientConfig',
          next: false
        },
        {
          title: 'Software',
          heading: 'Software Update',
          // body: 'Update Omega Software',
          component: 'SoftwareUpgrade',
          next: false
        },
        {
          title: 'Done',
          heading: 'Done',
          body: '<strong>You have successfully set up your Omega!</strong><br><br>Wondering what to do next? Check out our <a href="http://onioniot.com/project-blog" target="_blank">project blog</a> to get inspired, or head over to our <a href="http://onioniot.com/community" target="_blank">community forum</a> and talk to other Omega users.',
          done: true
        }
      ]
    }
  },
  methods: {
    getProgressColor: function (step) {
      if (step <= this.currentStep) {
        return '#9B4B63'
      } else {
        return '#bdc3c7'
      }
    },
    next: function () {
      this.currentStep += 1
    },
    done: function () {
      this.initialSetup = false
      // 0 means complete
      this.$store.dispatch('setInitialSetup', {
        value: 0
      })
      this.$store.commit('initialSetup', 0)
    }
  }
}
</script>

<style scoped>
.setup-component {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.setup-image > img {
  /* height: 200px; */
  height: 70px;
  width: auto;
  margin-bottom: 20px;
}
.setup-progress {
  min-height: 50px;
  padding: 20px 60px 40px 60px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress-title {
  padding-top: 20px;
  color: #bdc3c7;
}

.step-btn {
  display: flex;
  justify-content: center;
}

.modal {
  background: linear-gradient(230deg, #9B4B63, #854B70, #6A4D75, #4F4E72, #3A4C67, #2F4858);
  background-size: 300% 300%;

  -webkit-animation: MoveBG 10s ease infinite;
  -moz-animation: MoveBG 10s ease infinite;
  animation: MoveBG 10s ease infinite;

  /* background-size: cover; */
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding-top: 40px;
}

@-webkit-keyframes MoveBG {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes MoveBG {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes MoveBG {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

</style>



