<template>
  <div class="container">
    <div class="loading loading-lg" v-show="view.bLoading"></div>

    <!-- Wi-Fi - Configured Networks -->
    <div class="card" v-show="!view.bLoading && viewConfiguredNetworks && wifi.configured.length > 0">
      <div class="card-header">
        <div class="card-title h5">Wi-Fi Settings</div>
      </div>
      <div class="card-body">
        <table class="table table-hover">
          <tbody>
            <tr v-for="network in wifi.configured" :key="network.ssid" :class="{active: network.enabled}">
              <td>{{network.ssid !== '' ? network.ssid : 'hidden'}}</td>
              <td class="wifi-icons">
                <img v-if="network.encryption !== 'none'" src="~@/assets/img/icons/wifi_lock.svg" alt="">
                <img v-if="network.encryption === 'none'" src="~@/assets/img/icons/wifi.svg" alt="">
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- Wi-Fi - Scanned Networks -->
    <div class="card" v-show="!view.bLoading">
      <div class="card-header">
        <div class="card-title h5">Select a Network...</div>
      </div>
      <div class="card-body">
        <table class="table table-hover">
        <tbody>
          <tr v-for="network in wifi.scanned" :key="network.ssid" @click="selectedNetwork(network.ssid, network.encryption)">
            <td v-if="network.ssid.length > 0">{{network.ssid}}</td>
            <td v-else><span class="text-italic">Hidden</span></td>
            <td class="wifi-icons">
              <img v-if="network.encryption !== 'none'" src="~@/assets/img/icons/wifi_lock.svg" alt="">
              <img v-if="network.encryption === 'none'" src="~@/assets/img/icons/wifi.svg" alt="">
            </td>
          </tr>
        </tbody>
        </table>
      </div>
    </div>

    <!-- Wi-Fi - Add Network Modal -->
    <div class="modal" :class="wifiModalActive" id="modal-id">
      <a v-if="allowClosingModal" href="#close" class="modal-overlay" aria-label="Close" @click="closeWifiModal"></a>
      <a v-else class="modal-overlay"></a>
      <div class="modal-container">
        <div class="modal-header">
          <a v-if="allowClosingModal" href="#close" class="btn btn-clear float-right" aria-label="Close"  @click="closeWifiModal"></a>
          <div class="modal-title h5">{{view.modalTitle}}</div>
        </div>
        <div class="modal-body">
          <!-- loading symbol -->
          <div class="loading loading-lg" v-show="view.bModalLoading"></div>
          <!-- input form -->
          <div class="form-group" v-show="view.bModalForm && inputNetwork.encryption !== 'none'">
            <label class="form-label float-left" for="input-password">Enter the password for "{{inputNetwork.ssid}}"</label>
            <input class="form-input" :class="passwordFormClass" type="password" id="input-password" placeholder="Password" v-model="inputNetwork.password" @keyup.enter="joinNetwork">
            <p class="form-input-hint" v-show="passwordFormShowHint">{{passwordFormHintText}}</p>
          </div>
          <!-- text content -->
          <div class="content label" v-show="!view.bModalForm" v-html="view.modalText"></div>
        </div>
        <!-- buttons to join network or cancel -->
        <div class="modal-footer" v-show="view.modalButtons === 'join'">
          <button class="btn float-left"
          @click="setView('listView')"
          >Cancel</button>
          <button class="btn btn-primary" :class="joinButtonDisabled"
          @click="joinNetwork"
          >Join</button>
        </div>
        <!-- buttons to try again or skip step -->
        <div class="modal-footer" v-show="view.modalButtons === 'tryAgain' || view.modalButtons === 'skip'">
          <button class="btn btn-error float-left"
          v-show="view.modalButtons === 'tryAgain'"
          @click="setView('listView')"
          >Try Again</button>
          <button class="btn"
          @click="skipWifiConfig"
          >Skip Step</button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'WifiClientConfig',
  data () {
    return {
      wifi: {
        radio: 'ra0',
        configured: [],
        scanned: [],
        interval: {
          connectionCheck: 3,
          connectionTimeout: 45
        }
      },
      inputNetwork: {
        ssid: '',
        encryption: '',
        password: ''
      },
      view: {
        bLoading: true,
        bModalActive: false,
        bModalLoading: false,
        bModalForm: false,
        bModalCloseAllowed: true,
        modalButtons: '',
        modalTitle: '',
        modalText: ''
      }
    }
  },
  props: {
    viewConfiguredNetworks: Boolean,
    skipIfConnected: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    wifiModalActive () {
      return this.view.bModalActive ? 'active' : ''
    },
    passwordFormClass () {
      return (this.validatePassword(this.inputNetwork.encryption, this.inputNetwork.password).valid ? '' : 'is-error')
    },
    passwordFormShowHint () {
      // return !this.validatePassword(this.inputNetwork.encryption, this.inputNetwork.password).valid
      return true
    },
    passwordFormHintText () {
      var ret = this.validatePassword(this.inputNetwork.encryption, this.inputNetwork.password).err
      if (ret.length === 0) {
        ret = 'Password ok'
      }
      return ret
    },
    joinButtonDisabled () {
      return (this.validatePassword(this.inputNetwork.encryption, this.inputNetwork.password).valid ? '' : 'disabled')
    },
    allowClosingModal () {
      return this.view.bModalCloseAllowed
    }
  },
  methods: {
    /// view functions ///
    toggleLoading () {
      this.view.bLoading = !this.view.bLoading
    },
    toggleWifiModal () {
      this.view.bModalActive = !this.view.bModalActive
    },
    openWifiModal () {
      this.view.bModalActive = true
    },
    closeWifiModal () {
      this.view.bModalActive = false
    },
    setView (step) {
      switch (step) {
        case 'listView':
          this.closeWifiModal()
          break
        case 'selectedNetwork':
          this.view.modalTitle = 'WiFi Password'
          this.view.bModalForm = true
          this.view.bModalCloseAllowed = true
          this.view.modalButtons = 'join'
          // handle case where network has no encryption
          if (this.inputNetwork.encryption === 'none') {
            this.view.modalTitle = 'Join Network'
            this.view.bModalForm = false
            this.view.modalText = `Join "${this.inputNetwork.ssid}", an <strong>open, unencrypted</strong> network?`
          }
          // handle case where network has hidden ssid
          if (this.inputNetwork.ssid.length === 0) {
            this.view.modalTitle = 'This network has a hidden SSID'
            this.view.bModalForm = false
            this.view.modalText = `Please try connecting to a network with a visible SSID<br>or<br>connect to this network by following our <a href="http://onioniot.com/docs/omega2-docs/first-time-setup-command-line.html" target="_blank">guide to setup your Omega using the command line`
            this.view.modalButtons = ''
          }
          this.openWifiModal()
          break
        case 'joiningNetwork':
          this.view.bModalLoading = true
          this.view.bModalForm = false
          this.view.bModalCloseAllowed = false
          this.view.modalButtons = ''
          this.view.modalTitle = `Joining "${this.inputNetwork.ssid}" network`
          this.view.modalText = `If you were connected to your Omega's WiFi Access Point, your computer may disconnect during this process. Please make sure you're still connected!`
          break
        case 'connectionSuccess':
          this.view.bModalLoading = false
          this.view.bModalCloseAllowed = true
          this.view.modalButtons = ''
          this.view.modalTitle = `Joined "${this.inputNetwork.ssid}" network!`
          this.view.modalText = `Successfully connected to WiFi network`
          break
        case 'connectionFail':
          this.view.bModalLoading = false
          this.view.bModalCloseAllowed = false
          this.view.modalButtons = 'tryAgain'
          this.view.modalTitle = `Could not connect`
          this.view.modalText = `Something went wrong connecting to the "${this.inputNetwork.ssid}" network!<br><br>Press <strong>Try Again</strong> to attempt to connect to a different network, or try following our <a href="http://onioniot.com/docs/omega2-docs/first-time-setup-command-line.html" target="_blank">guide to setup your Omega using the command line</a>.`
          this.openWifiModal()
          break
        case 'skipStep':
          this.view.bModalLoading = false
          this.view.bModalCloseAllowed = false
          this.view.modalButtons = 'skip'
          this.view.modalTitle = `Already connected to the internet`
          this.view.modalText = `It looks like your Omega is already connected to the internet, let's skip to the next step`
          this.openWifiModal()
          break
        default:
          // do nothing
          break
      }
    },
    selectedNetwork (ssid, encryption) {
      this.inputNetwork.ssid = ssid
      this.inputNetwork.encryption = encryption
      this.inputNetwork.password = ''
      console.log(`selected network ${ssid}, encryption ${encryption}!`)
      // perform view changes
      this.setView('selectedNetwork')
    },
    /// device communication functions ///
    readNetworkList () {
      return new Promise(function (resolve, reject) {
        this.$store.dispatch('request', {
          object: 'onion',
          method: 'wifi-setup',
          params: {
            command: 'list',
            params: {}
          }
        })
          .then(resp => {
            if (resp.success) {
              resolve(resp.results)
            }
          })
          .catch(err => {
            reject(err)
          })
      }.bind(this))
    },
    scanForWifi () {
      return new Promise(function (resolve, reject) {
        this.$store.dispatch('request', {
          object: 'onion',
          method: 'wifi-scan',
          params: {
            device: this.wifi.radio
          }
        })
          .then(resp => {
            if (resp.results && resp.results instanceof Array) {
              resolve(resp.results)
            } else {
              reject()
            }
          })
          .catch(err => {
            reject(err)
          })
      }.bind(this))
    },
    internetConnectivityCheck () {
      return new Promise(function (resolve, reject) {
        this.$store.dispatch('request', {
          object: 'file',
          method: 'exec',
          params: {
            command: 'wget',
            params: ['--spider', 'http://repo.onioniot.com/']
          }
        })
          .then(result => {
            if (typeof (result) !== 'undefined' &&
              typeof (result.code) !== 'undefined' &&
              result.code === 0
            ) {
              resolve(true)
            } else {
              reject(false)
            }
          })
      }.bind(this))
    },
    addNetwork (ssid, encryption, password) {
      console.log('joining network')
      this.$store.dispatch('request', {
        object: 'onion',
        method: 'wifi-setup',
        params: {
          command: 'add',
          base64: true,
          params: {
            ssid: btoa(ssid),
            password: btoa(password),
            encr: btoa(encryption)
          }
        }
      })
        .then(function (response) {
          console.log()
        })
    },
    /// controller functions ///
    validatePassword (encryptionType, password) {
      var bValid = false
      var msg = ''
      switch (encryptionType) {
        case 'psk':
        case 'psk2':
          if (password.length < 8) {
            bValid = false
            msg = 'Password must be 8 characters or more'
          } else if (password.length > 63) {
            bValid = false
            msg = 'Password must be less than 64 characters'
          } else {
            bValid = true
          }
          break
        case 'wep':
          if (password.length !== 5 && password.length !== 13) {
            bValid = false
            msg = 'Password must be either 5 or 13 characters'
          } else {
            bValid = true
          }
          break
        case 'none':
          bValid = true
          break
        default:
          bValid = false
          msg = 'Unknown encryption type'
          break
      }
      return {
        valid: bValid,
        err: msg
      }
    },
    joinNetwork () {
      // issue command to join the network
      this.addNetwork(this.inputNetwork.ssid, this.inputNetwork.encryption, this.inputNetwork.password)
      // change the view
      this.setView('joiningNetwork')

      // start regular check to see if device responds and if it's connected to the internet
      var connectionCheckInterval = setInterval(function () {
        this.internetConnectivityCheck()
          .then(() => {
            // device is responding and is connected to the internet!
            clearInterval(connectionCheckInterval)
            clearTimeout(connectionCheckTimeout)
            console.log('Device successfully connected to the internet!!')
            // take action based on successfull connection!
            this.setView('connectionSuccess')
            setTimeout(() => {
              this.completeWifiConfig()
            }, 3000)
            // read the list of configured networks again
            this.readNetworkList()
              .then(networks => {
                this.wifi.configured = networks
              })
          })
          .catch(resp => {
            // do nothing!
          })
      }.bind(this), this.wifi.interval.connectionCheck * 1000)

      // start a timeout in case it doesn't connect in the allotted amount of time
      var connectionCheckTimeout = setTimeout(function () {
        // if we've reached this point, connection was not successful
        this.setView('connectionFail')
      }.bind(this), this.wifi.interval.connectionTimeout * 1000)
    },
    completeWifiConfig () {
      this.$emit('success')
    },
    skipWifiConfig () {
      this.$emit('success')
    }
  },
  created: function () {
    // fetch the wifi config data
    var p1 = this.readNetworkList()
    p1.then(networks => {
      this.wifi.configured = networks

      if (this.skipIfConnected && this.wifi.configured.length > 0) {
        console.log('have configured networks, checking for internet connectivity')
        // check for internet connectivity
        this.internetConnectivityCheck()
          .then(resp => {
            console.log('device already connected to internet, can skip wifi setup')
            this.setView('skipStep')
          })
          .catch(resp => {
            console.log('device not connected to internet!')
          })
      }
    })

    // start scanning for wifi networks
    var p2 = this.scanForWifi()
    p2.then(scannedNetworks => {
      // sort by signal strength
      function compare (a, b) {
        if (a.rssi <= b.rssi) {
          return -1
        }
        if (a.rssi > b.rssi) {
          return 1
        }
        return 0
      }
      this.wifi.scanned = scannedNetworks.sort(compare)
    })

    // only stop loading animation when both activities are complete
    Promise.all([p1, p2]).then(values => {
      this.toggleLoading()
    })
  }
}
</script>

<style scoped>
  .wifi-icons {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  .content {
    margin: 10px;
    padding: 10px;
  }

</style>



