<template>
  <div class="container">
    <div class="loading loading-lg upgrade-loading" v-show="view.isLoading"> </div>
    <div class="upgrade-message" v-html="view.msg"></div>

    <img v-show="view.bShowBlink" src="~@/assets/img/omega2_led.gif"/>

    <div class="bar progress-bar" v-show="view.bProgressBar">
      <div class="bar-item" :style="{width: `${progressPercent}%`}"></div>
    </div>

    <div class="container" v-show="!view.isLoading">

      <table class="table" v-show="view.bShowTable">
        <tbody>
          <!-- <tr>
            <td>Model</td>
            <td>Onion Omega2+</td>
          </tr> -->
          <tr>
            <td>Current Firmware</td>
            <td>{{currentFirmware}}</td>
          </tr>
          <tr>
            <td>Latest Firmware</td>
            <td>{{latestFirmware}}</td>
          </tr>
        </tbody>
      </table>

      <button class="btn btn-success upgrade-btn" v-show="view.bUpgradeButton" @click="upgrade">Update Now</button>
    </div>

    <span class="label message-box" v-show="view.labelText.length > 0" v-html="view.labelText"></span>
  </div>
</template>

<script>
export default {
  name: 'SoftwareUpgrade',
  data () {
    return {
      upgradeInfo: {},
      view: {
        isLoading: true,
        bShowTable: false,
        bUpgradeButton: false,
        bProgressBar: false,
        bShowBlink: false,
        msg: '',
        labelText: '',
        progress: {
          value: 0,
          max: 100
        }
      },
      interval: {
        install: 65
      }
    }
  },
  computed: {
    currentFirmware: function () {
      if (this.upgradeInfo.device) {
        return `${this.upgradeInfo.device.version} (b${this.upgradeInfo.device.build})`
      }
      return `X (bY)`
    },
    latestFirmware: function () {
      if (this.upgradeInfo.repo) {
        return `${this.upgradeInfo.repo.version} (b${this.upgradeInfo.repo.build})`
      }
      return `Z (bA)`
    },
    progressPercent: function () {
      return Math.round(this.view.progress.value * 100 / this.view.progress.max)
    }
  },
  methods: {
    /// view functions ///
    setView: function (state) {
      switch (state) {
        case 'check':
          this.view.isLoading = true
          this.view.bUpgradeButton = false
          this.view.bProgressBar = false
          this.view.msg = 'Checking if updates are available'
          break
        case 'needUpdate':
          this.view.isLoading = false
          this.view.bUpgradeButton = true
          this.view.msg = '<strong>New firmware is available!</strong><br>Update your Omega to the latest and greatest firmware to get all the newest software goodies from Onion.'
          this.view.labelText = 'Your Omega will download and install the latest firmware, this process will take about 5 minutes.<br>It\'s important that you <strong>do NOT unplug your Omega during the update</strong>!'
          break
        case 'onLatest':
          this.view.isLoading = false
          this.view.bUpgradeButton = false
          this.view.msg = 'Great, you are on the latest version, no updates required!'
          this.$emit('next')
          break
        case 'downloading':
          this.view.isLoading = true
          this.view.bUpgradeButton = false
          this.view.bProgressBar = true
          this.view.msg = 'Downloading updates, <strong>do NOT unplug your Omega!</strong>'
          this.view.labelText = ''
          break
        case 'installing':
          this.view.isLoading = true
          this.view.bUpgradeButton = false
          this.view.bProgressBar = true
          this.view.msg = 'Installing updates, <strong>do NOT unplug your Omega!</strong>'
          break
        case 'rebooting':
          this.view.isLoading = true
          this.view.bUpgradeButton = false
          this.view.bProgressBar = false
          this.view.msg = 'Omega is now <strong>initializing, do NOT unplug your Omega!</strong><br><br>Press Continue when your Omega stops blinking and remains on.'
          this.view.bShowBlink = true
          this.$emit('next')
          break
        default:
          // do nothing
          break
      }
    },
    setProgressBarMax: function (maxVal) {
      this.view.progress.max = maxVal
    },
    setProgressBarValue: function (value) {
      // console.log(`setting progress value to ${value}`)
      this.view.progress.value = value
    },
    /// controller functions ///
    upgrade: function () {
      this.$store.dispatch('setInitialSetup', {value: 0}) // 0 means complete
        .then(() => {
          return this.downloadFirmware()
        })
        .then(() => {
          return this.installFirmware()
        })
        .then(() => {
          this.setView('rebooting')
        })
    },
    downloadFirmware: function () {
      return new Promise(function (resolve, reject) {
        this.setView('downloading')
        // start the dl
        this.$store.dispatch('handleScript', {code: `wget -O ${this.upgradeInfo.image.local} ${this.upgradeInfo.image.url} `})
          .then(resp => {
            this.$store.dispatch('waitProcess', {pid: resp.pid})
              .then(() => {
                console.log('download complete')
                clearInterval(updateProgressbar)
                resolve()
              })
          })

        // use progress bar to track download based on filesize
        this.setProgressBarValue(0)
        this.setProgressBarMax(this.upgradeInfo.image.size)
        var updateProgressbar = setInterval(function () {
          this.$store.dispatch('request', {
            object: 'file',
            method: 'stat',
            params: {
              path: this.upgradeInfo.image.local
            }
          })
            .then(resp => {
              this.setProgressBarValue(resp.size)
            })
        }.bind(this), 250)
      }.bind(this))
    },
    installFirmware: function () {
      return new Promise(function (resolve, reject) {
        this.setView('installing')
        // start the upgrade procedure
        this.$store.dispatch('handleScript', {code: `sleep 1; sysupgrade ${this.upgradeInfo.image.local}`})
          .then(() => {
            // use the progress bar to count down 65 seconds
            var progressValue = 0
            this.setProgressBarValue(progressValue)
            this.setProgressBarMax(this.interval.install)
            var updateProgressbar = setInterval(function () {
              this.setProgressBarValue(progressValue++)
            }.bind(this), 1000)

            // move on to the next step
            setTimeout(function () {
              clearInterval(updateProgressbar)
              resolve()
            }, (this.interval.install + 5) * 1000)
          })
      }.bind(this))
    }
  },
  created: function () {
    this.setView('check')

    this.$store.dispatch('request', {
      object: 'onion',
      method: 'oupgrade',
      params: { params: {check: ''} }
    })
      .then(resp => {
        this.upgradeInfo = resp
        if (this.upgradeInfo.upgrade) {
          this.setView('needUpdate')
        } else {
          this.setView('onLatest')
          // setTimeout(() => {
          //   this.$emit('success')
          // }, 2000)
        }
      })
  }
}
</script>

<style scoped>
.upgrade-loading {
  width: 50%;
}

.table {
  max-width: 400px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upgrade-btn {
  margin-top: 20px;
}

.message-box {
  margin: 10px;
  padding: 10px;
}

.upgrade-message {
  margin: 10px;
  padding: 10px;
}

</style>



