<template>
  <div class="login-form" @keyup.enter="login">
        <div class="form-group">
          <label class="form-label" for="input-username">Username</label>
          <input class="form-input" type="text" id="input-username" v-model="username">
        </div>
        <div class="form-group">
          <label class="form-label" for="input-password">Password</label>
          <input class="form-input" type="password" id="input-password" v-model="password">
        </div>

        <button class="btn btn-success loginButton" @click="login">Login</button>
  </div>

</template>

<script>
export default {
  name: 'LoginForm',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login: function () {
      this.$store.dispatch('login', {username: this.username, password: this.password}).then(resp => {
        this.$emit('success')
      })
    }
  }
}
</script>

<style scoped>
.login-form{
  width: 100%;
  max-width: 400px;
}
.loginButton {
  width: 100%;
  margin-top: 20px;
}
</style>



